.wavecontainer {
    width: 100%;
    background: $color-sherpa;
    animation: colour 30s linear infinite;

    .block-links__items--red {
        background-color: transparent;
    }

    .block-links__items__anchor {
        border: 1px solid $color-white;

        @include media('>medium') {
            width: grid-column-width(4)-3;
        }

        &:after {
            background-color: none;
            height: 0;
        }
    }

    .block-links__items__anchor__header {
        padding-bottom: 0;
        font-weight: $font-weight-bold;
        font-size: $font-size-delta;
    }

    .block-links__items__anchor__text {
        margin: 0;
    }

    .ghost {
        @include anchor(ghost-full-highlight);

        @include media('<=small') {
            margin-bottom: 1.8rem;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    .ghost:nth-child(-n + 3) {
        @include media('>medium') {
            margin-bottom: 1.8rem;
        }
    }
}

#waves {
    position: absolute;
    height: 100%;
    width: 100%;
}

@-webkit-keyframes colour {
    0% {
        background: $color-sherpa;
    }

    40% {
        background: $color-prussia-light;
    }

    80% {
        background: $color-vivid;
    }

    100% {
        background: $color-sherpa;
    }
}

@-moz-keyframes colour {
    0% {
        background: $color-sherpa;
    }

    40% {
        background: $color-prussia-light;
    }

    80% {
        background: $color-vivid;
    }

    100% {
        background: $color-sherpa;
    }
}

@-o-keyframes colour {
    0% {
        background: $color-sherpa;
    }

    40% {
        background: $color-prussia-light;
    }

    80% {
        background: $color-vivid;
    }

    100% {
        background: $color-sherpa;
    }
}

@keyframes colour {
    0% {
        background: $color-sherpa;
    }

    40% {
        background: $color-prussia-light;
    }

    80% {
        background: $color-vivid;
    }

    100% {
        background: $color-sherpa;
    }
}
